
.download {
  text-align: center;
}
.download .icon-list {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  padding: 0;
  line-height: 1.5rem;
  color: #cac6db;
  font-size: 0.12rem;
  cursor: pointer;
}
.ds-icon-windows {
  background: url("../assets/windows.png") center 30% no-repeat;
}
.ds-icon-apple {
  background: url("../assets/apple.png") center 30% no-repeat;
}
.ds-icon-android {
  background: url("../assets/android.png") center 30% no-repeat;
}


