
.login-form .ds-icon-notice {
  padding: 0.08rem 0.15rem;
  padding-left: 0.39rem;
  line-height: 0.2rem;
  color: #fc0;
  text-align: left;
  border: 1px solid #fc0;
  border-radius: 0.05rem;
}
.login-form .ds-icon-notice + .ds-icon-notice {
  margin-top: 0.05rem;
}
.login-form dd {
  height: 0.5rem;
  padding: 0 0.15rem;
  padding-left: 0.49rem;
  border-bottom: 1px solid rgba(255,255,255,0.2);
}
.login-form dd:hover {
  border-bottom: 1px solid rgba(255,255,255,0.5);
}
.login-form dd.ds-icon-edit {
  position: relative;
}
.login-form dd.ds-icon-edit input {
  padding-right: 0.98rem;
}
.login-form dd.ds-icon-edit .ds-icon-code {
  position: absolute;
  bottom: 0.05rem;
  right: 0.15rem;
  display: inline-block;
  width: 0.83rem;
  height: 0.29rem;
  cursor: pointer;
}
.login-form dd.login {
  height: 0.36rem;
  line-height: 0.36rem;
  padding-left: 0;
  padding-right: 0;
  margin-top: 0.15rem;
}
.login-form dd input {
  height: 100%;
  line-height: 100%;
  border: 0;
  background: transparent;
  color: #fff;
  font-size: 0.14rem;
  letter-spacing: 0.02rem;
  width: 100%;
  box-sizing: border-box;
}
.login-form dd.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.login-form dd.disabled input {
  cursor: not-allowed;
}
.login-form dd.actions {
  border: 0;
  padding: 0.078rem 0;
}
.try {
  float: left;
}
.forget {
  float: right;
}


