
.step-tabs {
  text-align: center;
  transform: translateX(-0.18rem);
}
.step-tabs .tab {
  position: relative;
  display: inline-block;
  padding: 0.10005rem 0.5rem;
  color: #fff;
  background-color: rgba(255,255,255,0.2);
  border-color: rgba(255,255,255,0.2);
  margin-left: 0.21rem;
}
.step-tabs .tab:first-child {
  border-radius: 0.05rem;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.step-tabs .tab:not(:first-child):before {
  position: absolute;
  top: 0;
  left: -0.18rem;
  content: '';
  width: 0;
  height: 0;
  border-top: 0.185rem solid;
  border-bottom: 0.185rem solid;
  border-color: inherit;
  border-left: 0.185rem solid transparent;
  z-index: 1;
}
.step-tabs .tab:after {
  position: absolute;
  top: 0;
  left: 100%;
  content: '';
  width: 0;
  height: 0;
  border-top: 0.185rem solid transparent;
  border-bottom: 0.185rem solid transparent;
  border-left: 0.185rem solid;
  border-left-color: inherit;
  z-index: 1;
}
.step-tabs .tab.active {
  background-color: #1a9ff3;
  border-color: #1a9ff3;
}


