header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 0.3rem;
  z-index: 1;
}
header + .scroll-content {
  top: 0.3rem;
}
footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  min-width: 8.5rem;
  z-index: 1;
}
.scroll-content {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.scroll-content.has-footer {
  bottom: 0.68rem;
}
@media (max-width: 1362px) {
.scroll-content.has-footer {
    bottom: 1.36rem;
}
}
[class*=-enter],
[class*=-leave] {
  transition: all linear 0.2s;
}
.zoom-enter-active,
.zoom-leave-active {
  transform: perspective(500px) translateZ(-5000px);
}
.slide-enter,
.slide-leave-active {
  transform: translateX(100%);
}
.slide-up-enter,
.slide-up-leave-active {
  transform: translateY(-100%);
}
.slide-down-enter,
.slide-down-leave-active {
  transform: translateY(100%);
}
.slide-left-enter,
.slide-left-leave-active {
  transform: translateX(-100%);
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.back>.page[class*=-leave] {
  transition-delay: 0s;
  z-index: 1;
}
.back>.page[class*=-enter] {
  transform: none;
}


